
<template>
  <div id="app">
    <div id="PhotoSphereViewer" @touchstart="gtouchstart" @touchmove="gtouchmove" @touchend="gtouchend"></div>
    <!-- 定位层 -->
    <div class="Shop-utils" v-if="isShowUtils">

      <div class="Shop-utils__header">
        <div class="header-log"><img :src=" shopInfo.logo ||require('./assets/iconimg/log.png')" alt=""></div>
        <div class="header-address">
          <div class="header-address_top">
            <span>{{ shopInfo.name || '拉下贝尔'}}</span>
          </div>
          <div class="header-address_bottom">{{  shopInfo.addressName + shopInfo.address || '长河路88号世欧广场北区2层'}}</div>
        </div>
      </div>

      <!-- 查看更多引导下载app -->
      <div class="Shop-utils_btn" @click="goAPPstoreba" v-if="is_userAgent()">查看更多</div>

      <!-- 右边菜单 -->
      <div class="Shop-utils__aside" v-if="false">
        <ul>
          <li class="aside-item" @click="handleClick(item.title)" v-for="item in asideList" :key="item.icon">
            <div class="aside-item_icon">
              <img :src="item.icon" alt="">
            </div>
            <span>{{!isLogin? item.title: item.value}}</span>
          </li>
          <li class="aside-item" @click="handleClick('列表')">
            <div class="aside-item_icon">
              <img src="./assets/iconimg/shopList.png" alt="">
            </div>
          </li>
        </ul>
      </div>

      <!-- 底部说点什么 -->
      <div class="Shop-utils__footer" v-if="false">
        <div class="footer-input">
          <input type="text" placeholder="说点什么...">
        </div>

        <div class="footer-clear" @click="footerClear">
          <img src="./assets/iconimg/clear.png" alt="">
        </div>

      </div>

      <!-- 中间活动 固定位置-->
      <!-- 优惠卷 -->
      <div class="Shop-utils__coupon" v-if="false">
        <div class="coupon-box">
          <img src="./assets/iconimg/coupon.png" alt="">
        </div>
        <div class="coupon-del" @click="couponDel">
          <img src="./assets/iconimg/del.png" alt="">
        </div>
      </div>

      <!-- 浮框1 -->
      <div class="Shop-utils__floatBox" style="top:2.92rem" v-if="false">
        动态：今日人气王诞生！[图片]今日人气王诞生...
      </div>

      <!-- 浮框2 -->
      <div class="Shop-utils__floatBox" style="bottom:4.6rem;width:5rem" v-if="false">
        公告：店铺优惠券可领啦，门店使...
      </div>

      <!-- 滚动区域 消息组件 -->
      <div class="Shop-utils__msg" v-if="false">
        <tin-msg type='me'><template v-slot:name>我</template><template>llaal</template></tin-msg>
        <tin-msg><template v-slot:name>0000</template><template>XXXXXXXX</template></tin-msg>
        <tin-msg
          v-for="item in 10"
          :key="item"
         type='other'><template v-slot:name>0000</template><template>XXXX</template></tin-msg>
      </div>
      <!-- 滚动区域 消息组件上 -->
      <div class="Shop-utils__msg" style="bottom:5.26rem;" v-if="false">
        <tin-msg
          v-for="item in 4"
          :key="item"
         type='join'><template v-slot:name>0000</template><template>XXXX</template></tin-msg>
      </div>

    </div>
  </div>
</template>

<script>
import './utils/px2rem'
import { getUrlParam } from './utils/getUrlParam'
import PhotoSphereViewer from 'photo-sphere-viewer'
import CallApp from 'callapp-lib'
import TinMsg from './components/TinMsg'

const option = {
  scheme: {
    protocol: 'RenWaiRenApp.User.AppStore' // URL Scheme 的 scheme 字段，要打开的 APP 的标识
  },
  //   outChain: {
  //     protocol: 'RenWaiRenApp.Merchant.AppStore',
  //     path: 'temporary',
  //     key: 'url',
  //   },
  //   intent: {  //安卓原生谷歌浏览器必须传递 Intent 协议地址，才能唤起 APP
  //     package: 'bdapp://map/direction',
  //     scheme: 'bdapp',
  //   },
  //   universal: {
  //     host: 'flash-link.youku.com',
  //     pathKey: 'action',
  //   },
  timeout: 3000,
  appstore: 'https://apps.apple.com/cn/app/%E4%B9%B0%E6%9D%A1%E8%A1%97/id1484463343', // ios下载
  yingyongbao: '//a.app.qq.com/o/simple.jsp?pkgname=com.youku.shortvideo' // 安卓下载底
  // fallback: 'http://192.168.3.54:5500/delet/index.html', // 都失败走这里

}

export default {
  name: 'app',
  components: { TinMsg },
  data () {
    return {
      vrImgs: [],
      viewerId: 'PhotoSphereViewer',
      PSV: null,

      shopId: getUrlParam('id') || 1,
      shopInfo: {},

      // 其他
      asideList: [
        {
          icon: require('./assets/iconimg/zan.png'),
          title: '点赞',
          value: '3W'
        },
        {
          icon: require('./assets/iconimg/msg.png'),
          title: '评价',
          value: '1.8W'
        },
        {
          icon: require('./assets/iconimg/share.png'),
          title: '分享',
          value: '2635'
        }
      ],

      // 进入列表
      // 消息列表

      isZan: false,
      isLogin: false, // 是否已登录
      isCoupon: true, // 优惠卷
      isShowUtils: false,

      timeOutEvent: null

    }
  },

  created () {
    this.getShopInfo()
  },

  mounted () {
    this.initPhotoSphere()
    if (this.PSV) {
      // 全局事件
      this.PSV.on('click', (e) => { // 点击，则添加一个标记
        // console.log(e)
        // console.log(this.PSV.toggleAutorotate)
        // this.PSV.startAutorotate()
        // this.PSV.toggleAutorotate()
        // this.PSV.addMarker({
        //   id: '#' + Math.random(), // 标记的唯一标识
        //   tooltip: 'Generated marker',
        //   longitude: e.longitude,
        //   latitude: e.latitude,
        //   image: 'assets/iconimg/pin-blue.png',
        //   width: 32,
        //   height: 32,
        //   anchor: 'bottom center',
        //   data: {
        //     deletable: true
        //   }
        // })
      })

      this.PSV.on('select-marker', (marker, dblclick) => { // 选择某个标记
        // console.log('select-marker')

        // if (marker.data && marker.data.deletable) {
        //   if (dblclick) {
        //     this.PSV.removeMarker(marker)
        //   } else {
        //     this.PSV.updateMarker({
        //       id: marker.id,
        //       image: 'assets/iconimg/zaned.png'
        //     })
        //   }
        // }
      })

      this.PSV.on('over-marker', function (marker) { // 划过某个标记
        // console.log('划过', marker.id)
      })

      this.PSV.on('leave-marker', function (marker) { // 离开某个标记
        // console.log('离开', marker.id)
      })

      this.PSV.on('select-marker-list', function (marker) {
        // console.log('select-list', marker.id)
      })

      this.PSV.on('goto-marker-done', function (marker) {
        // console.log('goto-done', marker.id)
      })
    }
  },

  methods: {

    gtouchstart (e) {
      // 点击页面
      this.timeOutEvent = setTimeout(() => {
        this.timeOutEvent = 0
        // 真正长按后应该执行的内容

        // console.log('长按事件触发发')
      }, 500)// 这里设置定时器，定义长按500毫秒触发长按事件，时间可以自己改，个人感觉500毫秒非常合适
      return false
    },

    gtouchmove () {
      clearTimeout(this.timeOutEvent)// 清除定时器
      this.timeOutEvent = 0

      this.isShowUtils = false
    },

    gtouchend () {
      this.isShowUtils = true

      clearTimeout(this.timeOutEvent)// 清除定时器
      if (this.timeOutEvent !== 0) {
        // 这里写要执行的内容（尤如onclick事件）
        // console.log('你这是点击，不是长按')
      }
      return false
    },

    goAPPstoreba () {
      const lib = new CallApp(option)

      lib.open({
        path: 'UVRUpDataShopModelHome',
        param: {
          shopId: this.shopId
        }

      })
    },

    // 请求店铺
    getShopInfo () {
      this.$http.defaults.headers = {
        encrypt: 0,
        sign: JSON.stringify({ 'device': '6ac0e084-db92-e39f-0ef0-9f931d480353', 'platform': 'Android', 'version': '1.0' })
      }

      this.$http.get(
        '/api/shop/info',
        { params: { id: this.shopId } }
      ).then(res => {
        if (res.data.success) {
          this.shopInfo = res.data.data
          this.vrImgs = this.shopInfo.vrImgs.map(e => e.url)
          this.PSV.setPanorama(this.vrImgs[0], true, true).then(res => {
            this.isShowUtils = true
          })
        } else {
          console.log('店铺未找到')
        }
      })
    },

    // 其他
    initPhotoSphere () {
      this.PSV = new PhotoSphereViewer({
        panorama: this.vrImgs[0],
        container: this.viewerId,
        anim_speed: '1.1rpm', // 自动播放速度
        move_speed: 1.1, // 移动速度
        time_anim: false, // 全景图自动开始旋转之前的空闲时间（毫秒）。禁用为False。
        size: {
          height: '100%'
        },
        default_fov: 120,
        fisheye: true, // 使用true启用鱼眼效果或指定效果强度（true=1.0）此模式可能会对标记渲染产生副作用。
        navbar: false

        // 导航栏是一个数组，它可以包含以下核心按钮：自动旋转、缩放、下载、标记、陀螺仪、立体声、全屏，以及用于创建自定义按钮的标题和对象
        // navbar: [ 'autorotate', 'zoom', 'download', 'markers' ]
      })
    },

    handleClick (title) {
      if (!this.isLogin) {
        // console.log('没有登录')
        return
      }
      switch (title) {
        case '点赞':
          if (!this.isZan) {
            this.isZan = true
            this.asideList[0].icon = require('./assets/iconimg/zaned.png')
          } else {
            this.isZan = false
            this.asideList[0].icon = require('./assets/iconimg/zan.png')
          }
          break
        case '评价':
          console.log('评价')
          break
        case '分享':
          console.log('分享')
          break
        case '列表':
          console.log('列表')
          break
      }
    },

    // 清屏
    footerClear () {
      // console.log('清除列表数据')

      // 在这里测试
      // if (this.is_weixin()) {
      //   alert('点击右上角选择【在浏览器中】打开下载~~')
      //   return
      // }
      // const lib = new CallApp(option)
      // console.log(lib)

      // lib.open({
      //   path: ''
      // })
    },
    // 关闭优惠卷
    couponDel () {
      this.isCoupon = false
    },

    is_userAgent () {
      var u = navigator.userAgent
      if (u.indexOf('Android') > -1 || u.indexOf('Adr') > -1) {
        // false 为安卓端
        return false
      } else if (u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
        // true 为ios端
        return true
      }

      // isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1, // android终端

      // isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
    }

  }
}
</script>

<style lang="less">
*{
  box-sizing: border-box;
}
body, html{
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
}

#app {
  width: 100%;
  height: 100%;
  position: relative;
  font-size: .24rem;
}

img{
  width: 100%;
  height: 100%;
}

ul,li{
  list-style: none;
}

.Shop-utils{
  &__header{
    position: fixed;
    top: 0;
    left: 0;
    // z-index: 999;
    width: 100%;
    padding: .3rem;
    display: flex;
    align-items: center;
    color: #fff;
    .header-log{
      width: .8rem;
      height: .8rem;
      border-radius: 50%;
      overflow: hidden;
      margin-right: .19rem;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .header-address{
      &_top{
        line-height: 1;
        margin-bottom: .16rem;
        span:nth-child(1){
          font-size: .3rem;
          padding-right: .2rem;
        }

        span:nth-child(3){
          padding-left: .2rem;

        }
      }
      &_bottom{
        line-height: 1;
      }
    }
  }

  &__aside{
    position: fixed;
    right: .2rem;
    bottom: -.5rem;
    z-index: 999;

    display: flex;
    flex-direction: column;
    align-items: center;

    .aside-item{
      text-align: center;
      color: #fff;
      margin-bottom: .5rem;
      cursor: pointer;
      &_icon{
        width: .84rem;
        height: .84rem;
      }
    }
  }

  &__footer{
    position: fixed;
    left: 0.3rem;
    bottom: .2rem;
    z-index: 999;
    display: flex;
    align-items: center;

    .footer-input{
      width: 3rem;
      height: .6rem;
      border-radius: .3rem;
      padding:0 .29rem;
      background:rgba(22,26,36,0.3);
      input{
        border: none;
        outline: none;
        background: transparent;
        width: 100%;
        height: 100%;
        color: #fff;
        font-size: .24rem;
        &::-webkit-input-placeholder {
          color: #fff;
        }
      }
    }
    .footer-clear{
      width: .6rem;
      height: .6rem;
      margin-left: .2rem;
    }
  }

  &__coupon{
    position: fixed;
    top: 1.49rem;
    left: .12rem;
    z-index: 99;
    .coupon-box{
      width: 1rem;
      height: 1.13rem;
    }
    .coupon-del{
      position: absolute;
      top: -.15rem;
      right: -0.35rem;
      width: .44rem;
      height: .44rem;
      cursor: pointer;
    }
  }

  &__floatBox{
    position: fixed;
    // top: 2.92rem;
    left: .3rem;
    width: 4.60rem;
    height: .6rem;
    background:rgba(22,26,36,0.3);
    border-radius:.30rem;
    line-height: .6rem;
    font-size: .28rem;
    padding-left:.3rem;
    color: #FFFC00;
    white-space:nowrap;overflow:hidden;text-overflow:ellipsis;
  }

  &__msg{
    position: fixed;
    bottom: .8rem;
    left: .3rem;
    height: 4.5rem;
    width: 80%;
    display: flex;
    flex-direction: column-reverse;
    overflow: hidden;
    .tin-msg:nth-child(7){
      opacity: .5;
    }
    .tin-msg:nth-child(8){
      opacity: .1;
    }
    .tin-msg:nth-child(9){
      opacity: 0;
    }
  }

  &_btn{
    position: fixed;
    bottom: .67rem;
    left: 50%;
    z-index: 99;
    transform: translateX(-50%);
    width: 3.6rem;
    height: .9rem;
    font-size: .34rem;
    text-align: center;
    line-height: .9rem;
    color: #FFFFFF;
    background: #FC2D59;
    box-shadow: 0 9px 16px 0 rgba(201,45,78,0.64);
    border-radius: 50px;
    cursor: pointer;
  }
}

</style>
