<template>
  <div class="tin-msg">
        <span class="tin-msg-name" :class="type">
            <slot name="name"></slot>:
        </span>
        <span><slot></slot></span>
  </div>
</template>

<script>
// 1 user='me'
// 2 user='other' 默认
// 3 user='join'
export default {
  name: 'TinMsg',
  props: {
    type: {
      type: String,
      default: 'other'
    }
  }

}
</script>

<style lang="less">
.tin-msg{
  font-size: .28rem;
  color: #fff;
  margin-bottom: .16rem;
  &-name{
    margin-right: 15px;
  }
  &-name.me{
      color:#FF4466;
  }
  &-name.other{
      color:#F97F1A;
  }
  &-name.join{
      color:#FFFC00;
  }
}
</style>
